.header{background-color:#fff;padding:10px 25px;position:fixed;width:100%;height:60px;z-index:100;grid-area:header;display:flex;justify-content:space-between;align-items:center}.header_left{display:flex;align-items:center;gap:25px}.header_menu{font-size:19px;line-height:19px;padding:8px}.header_link{cursor:pointer;position:relative}.header_link::after{content:"RU";position:absolute;bottom:100%;left:100%;font-size:9.5px;color:gray;font-weight:500}.header_logo{width:70px;height:auto;position:relative}.header_search{width:40%}.header_log{display:flex;align-items:center;gap:5px;color:#6c6ce4;transition:.1s;font-weight:700;border-radius:50px;padding:5px 10px;background-color:#fff;border:1px solid #c1bdbd}.header_log_img{border-radius:50%;width:30px;height:auto}.header_log:hover{background-color:rgba(167,204,242,.405)}.header_log:active{background-color:rgba(167,204,242,.622)}.search{display:flex;align-items:center;gap:15px}.search_wrapper{display:flex;align-items:center;gap:10px;padding:10px 20px;border:1px solid #c1bdbd;border-radius:50px 0px 0px 50px;box-shadow:inset 1px 1px 3px 2px #dedbdb;width:100%}.search_flex{display:flex;width:100%}.search_input{height:20px;width:100%;font-size:16px}.search_input::placeholder{color:gray;font-size:16px}.search_findImg{display:none;font-size:16px}.search_btn{background-color:#e9e8e8;border-radius:0px 50px 50px 0px;border-right:1px solid #c1bdbd;border-top:1px solid #c1bdbd;border-bottom:1px solid #c1bdbd;padding:0px 18px;position:relative}.search_btn_img{font-size:16px}.search_btn::before{content:"Введите запрос";white-space:nowrap;background-color:rgba(93,92,92,.849);padding:10px 10px;font-size:11px;color:#fff;border-radius:6px;position:absolute;top:150%;left:50%;transform:translateX(-50%);display:none}.search_btn:hover::before{display:block}.search_btn:hover{background-color:#e1e0e0;box-shadow:.5px .5px 2px 1px rgba(204,204,204,.849)}.search_micro{font-size:25px}.search_clear{font-size:10px;transform:scale(1.5)}.inputFocus{position:relative;z-index:10;outline:1px solid #5d3fe2}